<template>
  <div class="hot">
    <div class="box" :style="{ 'background-image': `url(${bg})` }">
      <div class="img"></div>
      <p>更新时间: {{ time | toDate }}</p>
    </div>
    <div
      class="two"
      v-for="(item, index) in list"
      :key="index"
      @click="toMusic(item.id)"
    >
      <div class="left" v-text="index < 9 ? `0${index + 1}` : index + 1"></div>
      <div class="main">
        <p>{{ item.name }}</p>
        <span>
          <em>{{ item.ar[0].name }}</em>
          - {{ item.al.name }}
        </span>
      </div>
      <div class="right">
        <i>&#xe710;</i>
      </div>
    </div>
    <div class="three">
      <span>查看完整榜单</span>
      <i>&#xe731;</i>
    </div>
  </div>
</template>

<script>
let bg = require("../assets/hot_music_bg_2x.jpg");
// let sbg = require("../assets/index_icon_2x.png");
export default {
  name: "Hot",
  data() {
    return {
      bg: bg,
      time: "",
      list: []
    };
  },
  filters: {
    //过滤器
    toDate(val) {
      let now = new Date(val),
        m = now.getMonth() + 1,
        d = now.getDate();
      return m < 10 ? `0${m}月${d}日` : `${m}月${d}日`;
    }
  },
  created() {
    const baseUrl = "https://music.tryto.cn";
    let axios = this.$axios;
    //推荐歌单
    axios.get(baseUrl + "/top/list?idx=1").then(res => {
      console.log(res.data.playlist);
      this.time = res.data.playlist.updateTime;
      this.list = res.data.playlist.tracks;
      this.list.length = 20;
    });
  },
  methods: {
    toMusic(ids) {
      this.$router.push({
        path: "/music",
        query: {
          ids: ids
        }
      });
    }
  }
};
</script>

<style>
.hot {
  width: 100%;
}
.box {
  width: 100%;
  height: 290px;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 40px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 10px;
}
.img {
  width: 284px;
  height: 134px;
  background: url(../assets/index_icon_2x.png) no-repeat;
  background-size: 100%;
  background-position: -30px -38px;
}
.box p {
  color: hsla(0, 0%, 100%, 0.8);
  font-size: 22px;
  padding: 8px;
}
.two {
  width: 100%;
  height: 120px;
  display: flex;
}
.hot > .two:nth-child(-n + 4) .left {
  color: #d43c33;
}
.left {
  width: 90px;
  height: 100%;
  line-height: 120px;
  text-align: center;
}
.main {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-bottom: 2px solid #ececec;
}
.main p {
  font-size: 34px;
  padding-bottom: 6px;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.main span {
  font-size: 24px;
  color: #aaa;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.right {
  width: 90px;
  height: 100%;
  font-size: 60px;
  color: #aaa;
  line-height: 120px;
  text-align: center;
  border-bottom: 2px solid #ececec;
}
.three {
  width: 100%;
  height: 80px;
  margin: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #999;
  font-size: 28px;
}
.three i {
  font-size: 38px;
  color: #ccc;
}
</style>
