      <input class="sinput" type="text" placeholder="搜索歌曲、歌手、专辑" />
    </div>
    <div class="hotlist">
      <p class="hp">热门搜索</p>
      <div class="list">
        <span
          v-for="(item, index) in list1"
          :key="index"
          v-text="item.searchWord"
        ></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
