<template>
  <div class="recommend">
    <p>推荐歌单</p>
    <div class="one">
      <div
        class="imgbox"
        v-for="(item, index) in list1"
        :key="index"
        @click="toPlaylist(item.id)"
      >
        <img :src="item.picUrl" alt />
        <i class="erji"
          >&#xe607; {{ parseInt(item.playCount / 10000) + "万" }}</i
        >
        <p>{{ item.name }}</p>
      </div>
    </div>
    <p>最新音乐</p>
    <div
      class="two"
      v-for="(item, index) in list2"
      :key="index"
      @click="toMusic(item.id)"
    >
      <div class="songbox">
        <div class="song">
          <p>{{ item.name }}</p>
          <span>
            <em
              v-for="(sitem, index) in item.song.artists"
              :key="index"
              v-text="index > 0 ? ` / ${sitem.name}` : sitem.name"
            ></em>
            - {{ item.name }}
          </span>
        </div>
        <i>&#xe710;</i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Recommend",
  data() {
    return {
      list1: [],
      list2: []
    };
  },
  created() {
    const baseUrl = "https://music.tryto.cn";
    let axios = this.$axios;
    //推荐歌单
    axios.get(baseUrl + "/personalized?limit=6").then(res => {
      console.log(res.data.result);
      this.list1 = res.data.result;
    });
    //最新音乐
    axios.get(baseUrl + "/personalized/newsong").then(res => {
      // console.log(res.data.result);
      this.list2 = res.data.result;
    });
  },
  methods: {
    toMusic(ids) {
      this.$router.push({
        path: "/music",
        query: {
          ids: ids
        }
      });
    },
    toPlaylist(ids) {
      this.$router.push({
        path: "/playlist",
        query: {
          ids: ids
        }
      });
      console.log(1);
    }
  }
};
</script>

